		//保存个人资料 和 下一步
		saveUser(pddata) {
			const that = this;
			var postData = true;
			if (!code.validateEmpty(this.companySelected.label) || !code.validateEmpty(this.stationSelected.label)) {
				uni.showToast({
					title: '请选择报考单位和报考岗位',
